.wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: 900;
  font-family: 'Font Awesome 5 Free';
  line-height: 23px;
  position: fixed;
  right: 20px;
  bottom: 80px;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  background: #3291FF;
  color: #ffffff;
  box-shadow: 0 0 5px 0 rgb(22 119 255 / 80%);
}