.notes {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  padding: 10px 20px;
  border-bottom: 1px solid rgb(224, 224, 224);
}
.imgBox {
  display: flex;
  align-items: center;
}
.img {
  border-radius: 25px;
}
.notesInfo {
  margin: 0px 10px;
}
.notesWord {
  margin-top: 5px;
}
.names {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
}
.time {
  font-family: "Inter";
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  margin-top: 7px;
}
.noticeType {
  font-family: "DengXian";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
}
.unRead {
  display: flex;
  justify-content: flex-end;
  flex: 1;
}
