.main {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}
.img {
  width: 56px;
  height: 74px;
  margin-top: 20%;
  margin-bottom: 10px;
}
.avatar {
  border-radius: 50%;
}
.nickname {
  font-weight: 700;
  font-size: 18px;
}
.submit{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.button{
  width: 220px;
  height: 40px;
}
.input {
  display: flex;
  align-items: center;
  width: 220px;
  height: 40px;
  overflow: hidden;
}
.at {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 10px;
  background: #3291ff;
  border-radius: 10px 0 0 10px;
}
.account{
  height: 100%;
  border: 1px solid #3291ff;
  border-radius: 0 10px 10px 0;
}
