.main {
  width: 100vw;
  height: 100vh;
}
.img {
  position: absolute;
  width: 56px;
  height: 74px;
  top: 20%;
  left: 50%;
  transform: translate(-50%);
}
.button {
  background: #00BC0C;
  border-radius: 5px;
  width: 220px;
  height: 48px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.login{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content:center;
  gap: 10px;
  color: #ffffff;
  font-size: 18px;
}