.main {
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px 20px;
  border-bottom: 1px solid #ededed;
}

.search {
  --border-radius: 12px;
  --height: 28px;
  width: 85%;
}

.cancel {
  padding: 5px;
}

.content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.title {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  color: #000000;
  font-weight: 700;
}

.delete {
  padding: 10px;
}

.historyItem {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  padding: 10px 20px;
  font-size: 16px;
  color: #000000;
}

.historycontent {
  flex: 1;
}

.up {
  padding: 10px;
}
