.header {
  display: flex;
  align-items: center;
  height: 50px;
  border-bottom: 1px solid #ededed;
  padding: 0 20px;
}

.left {
  flex: 1;
}

.avatar {
  border-radius: 50%;
}

.topName {
  font-family: 'DengXian';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 17px;
}

.right {
  display: flex;
  flex: 1;
  justify-content: flex-end;
}

.info {
  padding: 21px 0px 0px 30px;
}

.nickname {
  margin-left: 6px;
  padding-top: 11px;
  font-weight: 900;
  font-size: 16px;
  color: #000000;
}

.account {
  margin-left: 6px;
  padding-top: 4px;
  font-weight: 400;
  font-size: 14px;
  color: #a9a9a9;
}

.follow {
  margin-left: 6px;
  padding-top: 16px;
  display: flex;
  gap: 15px;
}

.number {
  font-weight: 700;
}

.home {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 40px 36px 0px 0px;
  padding: 9px;
  border-radius: 5px;
  gap: 10px;
  &:hover {
    background-color: #ededed;
  }
}

.edit {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 15px;
  margin-right: 36px;
  padding: 9px;
  gap: 10px;
  border-radius: 5px;
  &:hover {
    background-color: #ededed;
  }
}

.quit {
  position: absolute;
  display: flex;
  width: 100%;
  padding: 0px 18px;
  bottom: 45px;
  .button {
    flex: 1;
    background: #eb4646;
    border-radius: 5px;
  }
}
