.main {
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.nav {
  padding: 20px;
  border-bottom: 1px solid #ededed;
}

.search {
  --border-radius: 12px;
  --height: 28px;
}

.content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.loading {
  display: flex;
  box-shadow: 0 0 5px rgb(0 0 0 / 10%);
  background: #fff;
  padding: 20px;
  .loading_avatar {
    --width: 50px;
    --height: 50px;
    --border-radius: 50%;
  }
  .loading_right {
    flex: 1;
    margin-left: 10px;
  }
  .loading_img {
    --width: 100%;
    --height: 162px;
    --border-radius: 5px;
    overflow: hidden;
    margin-top: 10px;
  }
}

.empty {
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
