.main {
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.nav {
  height: 50px;
  padding: 20px;
  border-bottom: 1px solid #ededed;
}

.main_content {
  flex: 1;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

.removePost {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin: 20px auto;
  width: 263px;
  height: 41px;
  color: #3291ff;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  border: 1px solid #ededed;
  border-radius: 5px;
}

.postDetailremove {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin: 20px auto;
  width: 263px;
  height: 41px;
  color: #3291ff;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  border: 1px solid #ededed;
  border-radius: 5px;
}

.commentWrap {
  display: flex;
  gap: 12px;
  padding: 20px;
  padding-bottom: 0;
}

.commentLeft {
  display: flex;
  flex-direction: column;
}

.verticalLine {
  margin: 3px 0;
  width: 50%;
  padding: 3px 0;
  border-right: 2px solid #EDEDED;
  flex: calc(100% - 6px);
}

.parentAvatar {
  border-radius: 25px;
}

.accountWrap {
  display: flex;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #a9a9a9;
}

.commentHeader {
  display: flex;
}

.original {
  margin: 10px 0px;
  padding: 15px;
  border: 1px solid #EDEDED;
  border-radius: 5px;
}

.originalInfo {
  display: flex;
  align-items: center;
}

.originalAvatar {
  margin-right: 14px;
}

.originalNick {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  margin-right: 5px;
}

.originalAccountWrap {
  display: flex;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #a9a9a9;
}

.originalWrap {
  margin: 10px 0px;
}

.originalContent {
  overflow: hidden;
  word-break: break-all;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  white-space: pre-wrap;
}

.originalImage {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  margin: 10px 0px;
  border-radius: 5px;
  overflow: hidden;
  max-height: 142px;
}

.originalImages {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: stretch;
  grid-gap: 4px;
  margin: 10px 0px;
  border-radius: 5px;
  overflow: hidden;
}

.originalImages>.originalImg {
  max-height: 74px;
}

.parentFooter {
  margin-top: 11px;
  margin-left: 4px;
  margin-bottom: 20px;
  display: flex;
  gap: 10px;
  color: #999999;
}

.comments,
.forwards,
.likes {
  display: flex;
  gap: 5px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  margin-right: 12px;
}

.postWrap {
  padding: 0 20px;
  padding-bottom: 11px;
}

.postHeader {
  display: flex;
}

.postAvatar {
  border-radius: 25px;
  margin-right: 10px;
}

.nickname {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  margin-bottom: 2px;
}

.account {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #a9a9a9;
}

.reply {
  margin: 6px 0;
}

.reply>span {
  font-family: 'DengXian';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #3291FF;
}

.content {
  overflow: hidden;
  word-break: break-all;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 7;
  margin-bottom: 10px;
  white-space: pre-wrap;
}

.image {
  border-radius: 10px;
  overflow: hidden;
  margin-top: 10px;
  max-height: 162px;
}

.images {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: stretch;
  grid-gap: 4px;
  margin: 10px 0px;
  border-radius: 10px;
  overflow: hidden;
}

.images>.img {
  max-height: 85px;
}

.postTime {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 15px;
  color: #666666;
  margin-top: 10px;
  margin-left: 5px;
}

.statistics {
  width: 100%;
  height: 44px;
  display: flex;
  align-items: center;
  padding-left: 32px;
  border-top: 1px solid #EDEDED;
  display: flex;
  align-items: center;
}

.count,
.text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
  margin-right: 3px;
}

.text {
  font-weight: 300;
  margin-right: 7px;
}

.postFooter {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 48px;
  gap: 80px;
  align-items: center;
  color: #999999;
  border-top: 1px solid #EDEDED;
  border-bottom: 1px solid #EDEDED;
}

.commentList {
  position: relative;
  min-height: 40vh;
}

.loading {
  display: flex;
  box-shadow: 0 0 5px rgb(0 0 0 / 10%);
  background: #fff;
  padding: 20px;

  .loading_avatar {
    --width: 50px;
    --height: 50px;
    --border-radius: 50%;
  }

  .loading_right {
    flex: 1;
    margin-left: 10px;
  }

  .loading_img {
    --width: 100%;
    --height: 162px;
    --border-radius: 5px;
    overflow: hidden;
    margin-top: 10px;
  }
}

.empty {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}