.postPopup {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 13px;
  font-family: 'DengXian';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 15px;
  color: #000000;
  border-bottom: 1px solid #e5e5e5;
}

.cancel {
  display: flex;
  margin-left: 7px;
  align-items: center;
}

.btn {
  min-width: 56px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'DengXian';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  background: #3291FF;
  color: #ffffff;
  border-radius: 12px;
}

.mainWrap {
  flex: 1;
  padding: 20px;
}

.commentWrap {
  display: flex;
  gap: 12px;
}

.postWrap {
  display: flex;
  gap: 12px;

}

.avatar {
  border-radius: 20px;
}

.right {
  flex: 1;
}

.commentLeft {
  display: flex;
  flex-direction: column;
}

.verticalLine {
  margin: 3px 0;
  width: 50%;
  padding: 3px 0;
  border-right: 2px solid #EDEDED;
  flex: calc(100% - 6px);
}

.commentHeader {
  display: flex;
}

.nickname {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  margin-right: 5px;
}

.accountWrap {
  display: flex;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #a9a9a9;
}

.contentWrap {
  margin: 10px 0px;
}

.content {
  overflow: hidden;
  word-break: break-all;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 7;
}

.image {
  border-radius: 5px;
  overflow: hidden;
  margin-top: 10px;
  max-height: 162px;
}

.images {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: stretch;
  grid-gap: 4px;
  margin: 10px 0px;
  border-radius: 5px;
  overflow: hidden;
}

.images>.img {
  max-height: 74px;
}

.reply {
  margin-bottom: 6px;
}

.reply>span {
  font-family: 'DengXian';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #3291FF;
}

.commentPost {
  margin-bottom: 39px;
}

.postInput {
  margin-bottom: 17px;
}

.footer {
  height: 40px;
  padding: 11px 31px;
  border-top: 1px solid #ebebeb;
}

.removePost {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin: 10px 0px;
  width: 263px;
  height: 41px;
  color: #3291ff;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  border: 1px solid #ededed;
  border-radius: 5px;
}

.parent {
  margin: 10px 0px;
  padding: 15px;
  border: 1px solid #EDEDED;
  border-radius: 5px;
}

.parentInfo {
  display: flex;
  align-items: center;
}

.parentAvatar {
  margin-right: 14px;
  border-radius: 12px;
}

.parentNick {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  margin-right: 5px;
}

.parentAccountWrap {
  display: flex;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #a9a9a9;
}

.parentWrap {
  margin: 10px 0px;
}

.parentContent {
  overflow: hidden;
  word-break: break-all;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
}

.parentImage {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  margin: 10px 0px;
  border-radius: 5px;
  overflow: hidden;
  max-height: 150px;
}

.parentImages {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: stretch;
  grid-gap: 4px;
  margin: 10px 0px;
  border-radius: 5px;
  overflow: hidden;
}

.parentImages>.parentImg {
  max-height: 65px;
}