//头部样式
.topBar {
  width: 100%;
  border-bottom: 1px solid rgb(224, 224, 224);
}

.topTime {
  display: flex;
  justify-content: space-between;
  padding: 15px;
  align-items: center;
}

.topNumber {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 15px;
  line-height: 18px;
}

.topBanner {
  display: flex;
  padding: 15px;
}

.topName {
  font-family: "DengXian";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 17px;
  margin: auto;
}

//主体样式
.topMain {
  height: 530px;
  display: flex;
  flex-direction: column-reverse;
  flex: 1;
  overflow: auto;
  width: 100%;
  background-color: rgb(244, 244, 244);
}

.bottomBanner {
  display: flex;
  width: 100%;
  height: 40px;
  align-items: center;
  padding: 2px 30px;
  background-color: rgb(255, 255, 255);
  position: fixed;
  bottom: 49px;
}

.bottomWord {
  width: 70%;
  margin: auto;
  background: #ededed;
  border-radius: 14px;
  padding-left: 15px;
  font-size: 14px;
}

.dialogLeft {
  width: max-content;
  background-color: rgb(255, 255, 255);
  padding: 5px 20px;
  border-radius: 8px;
  border-bottom-left-radius: 0;
  margin: 20px;
  max-width: 70%;
  word-break: break-all;
}

.dialogBox {
  display: flex;
  justify-content: flex-end;
  margin: 14px;
}

.dialogRight {
  width: max-content;
  background-color: #3291ff;
  color: #fff;
  padding: 5px 20px;
  border-radius: 8px;
  border-bottom-right-radius: 0;
  max-width: 70%;
  word-break: break-all;
}

.time {
  font-family: 'Mulish';
  font-style: normal;
  font-size: 12px;
  color: #999999;
}

.dialogRight>.time {
  text-align: right;
  color: #fff;
}

.bottomBtn {
  border-radius: 10px;
}

// antd中input框样式
:global(.adm-input-element) {
  font-size: 12px;
}

.img {
  :global(.adm-image-uploader-upload-button-wrap) {
    position: fixed;
    bottom: 58px;
    left: 120px;
  }
}

.wrap {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding-bottom: 40px;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #ededed;
  padding: 10px 20px;
}

.cancel {
  padding: 10px;
}

.sure {
  min-width: 70px;
  height: 30px;
  line-height: 0;
  background: #3291ff;
  border-radius: 15px;
}

.img {
  :global(.adm-image-uploader-cell) {
    height: fit-content !important;
    max-height: 400px;
  }
}

.popWrap {
  min-height: 50vh;
  display: flex;
  flex-direction: column;
}

.popbody {
  flex: 1;
  display: flex;
  align-items: center;
  margin: 0 auto;
}

.loader {
  text-align: center;
}

.imgMessage {
  padding: 5px;
}

.unreadLine {
  text-align: center;
  font-size: 14px;
  color: #aaaaaa;
}

:global(.adm-popover-menu-list) {
  overflow: hidden;
  min-width: 70px;
}

