.main {
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.title {
  font-weight: 700;
  font-size: 16px;
  color: #000000;
}

.banner {
  width: 100%;
  height: 120px;
}

.swap {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

.avatar {
  position: relative;
  width: 54px;
  height: 54px;
  left: 20px;
  transform: translate(0, -50%);
  border-radius: 50%;
  overflow: hidden;
}

.form {
  .adm-list-item {
    padding: 0;
  }
}

.nickname {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 15px;
  padding: 15px 20px;
}

.bio {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 15px 20px;
  gap: 15px;
}

.content {
  flex: 1;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #ededed;
  padding: 10px 20px;
}

.cancel {
  padding: 10px;
}

.sure {
  width: 70px;
  height: 30px;
  line-height: 0;
  background: #3291ff;
  border-radius: 15px;
}

.img {
  :global(.adm-image-uploader-cell) {
    height: fit-content !important;
    max-height: 136px;
  }
}

.popWrap {
  min-height: 50vh;
  display: flex;
  flex-direction: column;
}

.popbody {
  flex: 1;
  display: flex;
  align-items: center;
  margin: 0 auto;
}