.userItem {
  display: flex;
  overflow: hidden;
  background: #fff;
  padding: 20px;
}

.left {
  margin-right: 10px;
}

.avatar {
  border-radius: 50%;
}

.right {
  width: 100%;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.nickname {
  font-weight: 700;
  font-size: 16px;
}

.account {
  font-weight: 400;
  font-size: 14px;
  color: #a9a9a9;
}

.follower {
  border-radius: 13px;
  border: 1px solid #cccccc;
}

.follow {
  background: #000000;
  border-radius: 13px;
  color: #ffffff;
  width: 80px;
  height: 26px;
}

.content {
  margin-top: 10px;
  font-weight: 400;
  font-size: 12px;
  color: #333333;
}
