.dialogs {
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
  padding: 10px 20px;
}
.info {
  margin: 0px 10px;
  overflow: hidden;
  flex: 1;
}
.name {
  font-family: "DengXian";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 10px;
}
.word {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #adb5bd;
  font-family: "Mulish";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
}
.minute {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}
.count {
  display: flex;
  justify-content: flex-end;
  height: 24px;
  visibility: hidden;
}
.unreadCount {
  display: flex;
  justify-content: flex-end;
}
.img {
  border-radius: 25px;
}
//组件库原始样式
:global(.adm-tag-round) {
  border-radius: 100px;
  padding: 3px 6px;
}
