.main {
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.header {
  position: relative;
  width: 100%;
}
.nav {
  display: flex;
  justify-content: space-between;
  height: 90px;
  padding: 20px;
}
.bag {
  display: flex;
  align-items: center;
  justify-content: center;
  top: 13px;
  width: 30px;
  height: 30px;
  background: rgba(0, 0, 0, -0.5);
  border-radius: 50%;
  .svg {
    color: #ffffff;
  }
}
.avatar {
  position: absolute;
  left: 20px;
  top: 60px;
  border-radius: 50%;
}
.nickname {
  position: absolute;
  left: 25px;
  top: 125px;
  font-weight: 900;
  font-size: 16px;
}
.concern {
  position: absolute;
  width: 80px;
  height: 25px;
  right: 20px;
  top: 103px;
  border-radius: 13px;
}
.info {
  padding: 65px 20px 10px 20px;
}
.ac_tm {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  color: #666666;
}
.time {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5px;
  .svg {
    width: 16px;
  }
}
.bio {
  word-break: break-all;
  color: #333333;
}
.follow {
  display: flex;
  gap: 15px;
}
.number {
  font-weight: 700;
}
.content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
}
.loading {
  display: flex;
  box-shadow: 0 0 5px rgb(0 0 0 / 10%);
  background: #fff;
  padding: 20px;
  .loading_avatar {
    --width: 50px;
    --height: 50px;
    --border-radius: 50%;
  }
  .loading_right {
    flex: 1;
    margin-left: 10px;
  }
  .loading_img {
    --width: 100%;
    --height: 162px;
    --border-radius: 5px;
    overflow: hidden;
    margin-top: 10px;
  }
}
.empty {
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
