.postItem {
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 5px rgb(0 0 0 / 10%);
  background: #fff;
  padding: 20px;
}

.left {
  margin-right: 10px;
}

.right {
  flex: 1;
}

.header {
  display: flex;
}

.nickname {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  margin-right: 5px;
}

.accountWrap {
  display: flex;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #a9a9a9;
}

.moreAction {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}

.avatar {
  border-radius: 25px;
}

.parentAvatar {
  border-radius: 12px;
}

.reply {
  margin: 6px 0;
}

.reply>span {
  font-family: 'DengXian';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #3291FF;
}

.contentWrap {
  margin: 10px 0px;
}

.content {
  overflow: hidden;
  word-break: break-all;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 7;
  white-space: pre-wrap;
}

.image {
  border-radius: 5px;
  overflow: hidden;
  margin-top: 10px;
  max-height: 162px;
}

.images {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: stretch;
  grid-gap: 4px;
  margin: 10px 0px;
  border-radius: 5px;
  overflow: hidden;
}

.images>.img {
  max-height: 74px;
}

.removePost {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin: 10px 0px;
  width: 263px;
  height: 41px;
  color: #3291ff;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  border: 1px solid #ededed;
  border-radius: 5px;
}

.footer {
  display: flex;
  gap: 10px;
  color: #999999;
}

.comments {
  display: flex;
  gap: 5px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  margin-right: 12px;
}

.forwards {
  display: flex;
  gap: 5px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  margin-right: 12px;
}

.likes {
  display: flex;
  gap: 5px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
}

.parent {
  margin: 10px 0px;
  padding: 15px;
  border: 1px solid #EDEDED;
  border-radius: 5px;
}

.parentInfo {
  display: flex;
  align-items: center;
}

.parentAvatar {
  margin-right: 14px;
}

.parentNick {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  margin-right: 5px;
}

.parentAccountWrap {
  display: flex;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #a9a9a9;
}

.parentWrap {
  margin: 10px 0px;
}

.parentContent {
  overflow: hidden;
  word-break: break-all;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  white-space: pre-wrap;
}

.parentImage {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  margin: 10px 0px;
  border-radius: 5px;
  overflow: hidden;
  max-height: 150px;
}

.parentImages {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: stretch;
  grid-gap: 4px;
  margin: 10px 0px;
  border-radius: 5px;
  overflow: hidden;
}

.parentImages>.parentImg {
  max-height: 65px;
}